import * as tslib_1 from "tslib";
import { Component, Vue } from "vue-property-decorator";
import { namespace } from "vuex-class";
import http from "@/utils/http";
import { lan, getQueryVariable } from "@/utils/bower.ts";
const Common = namespace("common");
let Receive = class Receive extends Vue {
    constructor() {
        super(...arguments);
        this.countryArr = [];
        this.isShowCountryList = false;
        this.language = "en";
        this.selectCountry = null;
        this.phoneNumber = "";
        this.verifyCode = "";
        this.timedownTotal = 60;
        this.timedownText = "";
    }
    mounted() {
        this.language = lan();
        this.getCountryList();
    }
    get verifyCodeText() {
        return String(this.$t("receive_get_code"));
    }
    isShowCountry() {
        this.isShowCountryList = !this.isShowCountryList;
    }
    selectCountryMethod(item) {
        this.selectCountry = item;
        this.isShowCountryList = false;
    }
    async getVerifyCode() {
        let code = getQueryVariable("code");
        if (this.timedownTotal == 60) {
            if (code) {
                let res = await http.post("/promotion/phone/code", {
                    country_code: this.selectCountry.country_code,
                    phone_no: this.phoneNumber,
                    invite_code: code
                });
                if (res.status == 200) {
                    this.timedown();
                }
                else {
                    this.$message({
                        message: res.data.messages,
                        center: true,
                        type: "error"
                    });
                }
            }
            else {
                this.$message({
                    message: "invitation code should not be null",
                    center: true,
                    type: "error"
                });
            }
        }
    }
    timedown() {
        this.timedownTotal = this.timedownTotal - 1;
        this.timedownText = String(this.$t("timedown_text", { s: this.timedownTotal }));
        if (this.timedownTotal <= 0) {
            this.timedownTotal = 60;
            return;
        }
        else {
            setTimeout(() => {
                this.timedown();
            }, 1000);
        }
    }
    async submit() {
        let code = getQueryVariable("code");
        if (code && this.phoneNumber.length > 0 && this.verifyCode.length > 0) {
            let res = await http.post("/promotion/phone/verify", {
                country_code: this.selectCountry.country_code,
                phone_no: this.phoneNumber,
                invite_code: code,
                verify_code: this.verifyCode
            });
            if (res.status == 200) {
                this.$message({
                    message: String(this.$t('receive_success')),
                    center: true,
                    type: "success"
                });
            }
            else {
                this.$message({
                    message: res.data.messages,
                    center: true,
                    type: "error"
                });
            }
            console.log(res);
        }
        else {
            this.$message({
                message: "Phone number or verification code cannot be empty",
                center: true,
                type: "error"
            });
        }
    }
    async getCountryList() {
        let res = await http.get("/country");
        res.data.forEach((element) => {
            switch (Number(element.country_calling_code)) {
                case 86:
                    element.country_name_cn = "中国";
                    break;
                case 1:
                    if (element.country_name == "Canada") {
                        element.country_name_cn = "加拿大";
                    }
                    else {
                        element.country_name_cn = "美国";
                    }
                    break;
                case 49:
                    element.country_name_cn = "德国";
                    break;
                case 33:
                    element.country_name_cn = "法国";
                    break;
                case 44:
                    element.country_name_cn = "英国";
                    break;
                case 852:
                    element.country_name_cn = "香港";
                    break;
                case 62:
                    element.country_name_cn = "印度尼西亚";
                    break;
                case 82:
                    element.country_name_cn = "韩国";
                    break;
                case 853:
                    element.country_name_cn = "澳门";
                    break;
                case 60:
                    element.country_name_cn = "马来西亚";
                    break;
                case 234:
                    element.country_name_cn = "尼日利亚";
                    break;
                case 63:
                    element.country_name_cn = "菲律宾";
                    break;
                case 7:
                    element.country_name_cn = "俄罗斯";
                    break;
                case 65:
                    element.country_name_cn = "新加坡";
                    break;
                case 66:
                    element.country_name_cn = "泰国";
                    break;
                case 886:
                    element.country_name_cn = "台湾";
                    break;
                case 84:
                    element.country_name_cn = "越南";
                default:
                    break;
            }
        });
        this.countryArr = res.data;
        this.selectCountry = this.countryArr[0];
    }
};
Receive = tslib_1.__decorate([
    Component
], Receive);
export default Receive;
