import Vue from 'vue';
import Router from 'vue-router';
import Collection from './views/collection/collection.vue';
import Receive from './views/receive/receive.vue';
Vue.use(Router);
export default new Router({
    routes: [
        {
            path: '/',
            name: 'collection',
            component: Collection
        },
        {
            path: '/receive',
            name: 'receive',
            component: Receive
        }
    ]
});
